}

.header {
  margin: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}

hr {
  width: 30%;
  height: 3px;
  background-color: #b61818;
  border: 0;
}

.Navbar .header i {
  margin: auto;
  position: relative;
  top: 30px;
  left: -110px;
  -webkit-transform: rotate(25deg);
          transform: rotate(25deg);
}

/* The switch - the box around the slider */
.switch {
  position: relative;
  display: inline-block;
  width: 32px;
  height: 18px;
}

/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 2px;
  bottom: 2px;
  background-color: #2e2e2e;
  transition: .4s;
}

input:checked+.slider {
  background-color: #85868b;
}

input:focus+.slider {
  box-shadow: 0 0 1px #85868b;
}

input:checked+.slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(14px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.fa-sun,
.fa-moon {
  padding-right: 10px;
}

