.Footer {
  padding-top: 20px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: flex-end;
  margin-bottom: 20px;
}

.random-button {
  padding: 20px 40px;
  background-color: #3d3d3d;
  border: none;
  border-radius: 7px;
  color: white;
  margin-bottom: 20px;
  font-size: 20px;
}

.view-all-button {
  padding: 10px 20px;
  background-color: #3d3d3d;
  border: none;
  border-radius: 7px;
  color: white;
  margin-bottom: 40px;
  font-size: 12px;
}

button:hover {
  cursor: pointer;
}

a {
  text-decoration: none;
  color: #fff;
}

.Footer .contact {
  margin-bottom: 20px;
}